.box div{
  position:absolute;
  width: 100%;
  height: 100%;
  background-image: url("../src/img/ps.PNG");
  background-size: 210;
  background-position: top center;
  background-repeat: no-repeat;
  animation:up-downn 2s ease-in-out infinite alternate-reverse both ; 
}
@media screen and (max-width: 800px) {
  .box div:nth-child(1) {
    display: none;
  }
}
@media screen and (max-width: 800px) {
  .box div:nth-child(7) {
    display: none;
  }
}
.box div:nth-child(1){
  position:absolute;
  background-image: url("../src/img/pr.PNG");
  background-size: 150px;
  background-position: top left;
  background-repeat: no-repeat;
  animation:up-down 8s ease-in-out infinite alternate-reverse both ;
}
.box div:nth-child(2){
  position:absolute;
  background-image: url("../src/img/react.png");
  background-size: 100px;
  background-position: top right;
  background-repeat: no-repeat;
  animation:up-downn 2s ease-in-out infinite alternate-reverse both ;
}
.box div:nth-child(3){
  position:absolute;
  background-image: url("../src/img/babel.png");
  background-size: 100px;
  background-position: left center;
  background-repeat: no-repeat;
  animation:up-downn 2s ease-in-out infinite alternate-reverse both ;
}
.box div:nth-child(4){
  position:absolute;
  background-image: url("../src/img/and.jpg");
  background-size: 100px;
  background-position: left bottom;
  background-repeat: no-repeat;
  animation:up-downn 2s ease-in-out infinite alternate-reverse both ;
}
.box div:nth-child(5){
  position:absolute;
  background-image: url("../src/img/react.png");
  background-size: 80px;
  background-position: center center;
  background-repeat: no-repeat;
  animation:up-down 8s ease-in-out infinite alternate-reverse both ;
}
.box div:nth-child(6){
  position:absolute;
  background-image: url("../src/img/pr.PNG");
  background-size: 110px;
  background-position:bottom right;
  background-repeat: no-repeat;
  animation:up-down 8s ease-in-out infinite alternate-reverse both ;
}
.box div:nth-child(7){
  position:absolute;
  background-image: url("../src/img/babel.png");
  background-size: 200px;
  background-position: 25% 75%;
  background-repeat: no-repeat;
  animation:up-down 6s ease-in-out infinite alternate-reverse both ;
}
.box div:nth-child(8){
  position:absolute;
  background-image: url("../src/img/and.jpg");
  background-size:50px;
  background-position: 75% 25%;
  background-repeat: no-repeat;
  animation:up-downn 2s ease-in-out infinite alternate-reverse both ;
}
.box div:nth-child(9){
  position:absolute;
  background-image: url("../src/img/babel.png");
  background-size: 200px;
  background-position:  75% 75%;
  background-repeat: no-repeat;
  animation:up-downn 2s ease-in-out infinite alternate-reverse both ;
}

@-webkit-keyframes up-down{
  0%{
    transform: translateY(70px);
  }
  100%{
    transform: translateY(-70px);
  }
}

@keyframes up-down{
  0%{
    transform: translateY(70px);
  }
  100%{
    transform: translateY(-70px);
  }
}
@-webkit-keyframes up-downn{
  0%{
    transform: translateY(20px);
  }
  100%{
    transform: translateY(-20px);
  }
}

@keyframes up-downn{
  0%{
    transform: translateY(20px);
  }
  100%{
    transform: translateY(-20px);
  }
}

