/* width */
::-webkit-scrollbar {
  width: 10px;
  background-color: #0A192F;
  
}

/* Track */
::-webkit-scrollbar-track {
  
  border-radius: 40px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background:#64ffda;
  border-radius: 40px;
  box-shadow: inset 0 0 5px grey; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #64ffda; 
}
.card{

